import * as React from 'react';
import { Link, useStaticQuery, graphql } from 'gatsby';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGithub, faInstagram, faLinkedin, faTwitter } from "@fortawesome/free-brands-svg-icons";
import { faMapMarkerAlt, faEnvelope } from '@fortawesome/free-solid-svg-icons';
import {
  container,
  sidenav,
  profileAvatar,
  nameBox,
  textCenter,
  mapIcon,
  background,
  navbarLine,
} from './layout.module.css';

const Layout = ({ pageTitle, children }) => {
  const data = useStaticQuery(graphql`
    query{
      site {
        siteMetadata {
          title
        }
      }
      allMdx(sort: { frontmatter: { date: DESC }}) {
        nodes {
          frontmatter {
            date(formatString: "MMMM D, YYYY")
            title
            slug
          }
          id
          excerpt
        }
      }
    }
  `)
  return (
    <div className={background}>
      {/* SideBar */}
      <div className={sidenav}>
        <div className={profileAvatar} />
        <div className={nameBox}>Yihua Liu</div>
        <a href="/#"><Link to='/'>About</Link></a>
        {
          data.allMdx.nodes.map(node => (
            <a href="/#">
              <Link to={`/${node.frontmatter.slug}`}>
                {/* {node.frontmatter.title} */}
                Portfolio
              </Link>
            </a>
          ))
        }
        <a href="/#"><Link to='/blog'>Blog</Link></a>
        <div className={textCenter}>
          <ul>
            <li><a href="https://github.com/yihuajack"><FontAwesomeIcon icon={faGithub} color="black" /></a></li>
            <li><a href="https://twitter.com/yihuajack"><FontAwesomeIcon icon={faTwitter} color="black" /></a></li>
            <li><a href="https://www.instagram.com/ayka_tsuzuki/"><FontAwesomeIcon icon={faInstagram} color="black" /></a></li>
            <li><a href="https://www.linkedin.com/in/%E7%BF%8A%E5%8D%8E-%E5%88%98-831842198/"><FontAwesomeIcon icon={faLinkedin} color="black" /></a></li>
          </ul>
        </div>
        <div className={navbarLine}>
          <div className={mapIcon}><FontAwesomeIcon icon={faMapMarkerAlt} color="black" />
          </div>Shanghai, China
        </div>
        <div className={navbarLine}>
          <div className={mapIcon}><FontAwesomeIcon icon={faEnvelope} color="black" />
          </div><a href="mailto:yihuajack@live.cn">yihuajack@live.cn</a>
        </div>
      </div>
      {/* MainPage */}
      <div className={container}>
        <title>{pageTitle} | {data.site.siteMetadata.title}</title>
        {/* <header className={siteTitle}>{data.site.siteMetadata.title}</header> */}
        <main>
          {children}
        </main>
      </div>
    </div>
  )
}

export default Layout
